<template>
  <div id="modalDeleteTransaction" ref="deleteTransactionModal" class="modal">
    <div class="modal-content">
      <h4>Удаление операции</h4>
      <div class="row">
        <div class="col">
          <p>{{
              selectedTransaction?.type === 'income' ?
                'Доход' : selectedTransaction?.type === 'outcome' ?
                  'Расход' : 'Перевод'
            }} на сумму: {{ currencyFilter(selectedTransaction?.amount) }}</p>
          <p>Дата: {{ transactionDate }}</p>
          <p>{{
              selectedTransaction?.type === 'transfer' ?
                'Счёт-отправитель' : 'Счёт'
            }}:
            {{ selectedTransaction?.account }}</p>
          <p>{{
              selectedTransaction?.type === 'transfer' ?
                'Счёт-получатель' : 'Категория'
            }}:
            {{ selectedTransaction?.category }}</p>
          <p>Операция создана: {{ selectedTransaction?.author }}</p>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <p class="red-text">Это действие необратимо</p>
        </div>
      </div>
      <button class="btn modal-close-btn modal-close"><i class="large material-icons">close</i>
      </button>
      <div class="row">
        <div class="col">
          <button
            class="btn red"
            data-target="modalAddAccount"
            @click.prevent="deleteHandler">
            <i class="material-icons right">delete_forever</i>
            Удалить навсегда
          </button>
        </div>
        <div class="col">
          <button
            class="btn modal-close"
            data-target="modalAddAccount">
            <i class="material-icons right">cancel</i>
            Отменить удаление
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { ref, onMounted, onBeforeUnmount, onBeforeUpdate } from 'vue';
import { useTransactionsStore } from '@/stores/transactions';
import { useFiltersStore } from '@/stores/filters';
import { storeToRefs } from 'pinia';

const { deleteTransaction } = useTransactionsStore();
const { selectedTransaction } = storeToRefs(useTransactionsStore());
const { dateFilter, currencyFilter } = useFiltersStore();

const deleteTransactionModal = ref();
const modalInstance = ref();

const deleteHandler = () => {
  deleteTransaction();
  modalInstance.value.close();
};

onMounted(() => {
  modalInstance.value = M.Modal.init(deleteTransactionModal.value);
});

let transactionDate: string;

onBeforeUpdate(() => {
  if (!!selectedTransaction.value) {
    transactionDate = dateFilter(selectedTransaction.value.date);
  } else {
    transactionDate = '';
  }
});

onBeforeUnmount(() => {
  modalInstance.value.destroy();
});
</script>

<style lang="scss" scoped>
.modal-content {
  position: relative;

  @media (orientation: portrait) and (max-width: 600px) {
    padding-top: 50px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 24px;
  right: 24px;

  @media (orientation: portrait) and (max-width: 600px) {
    top: 10px;
    right: 10px;
  }
}
</style>