import { defineStore, storeToRefs } from 'pinia';
import { computed, ComputedRef, Ref, ref } from 'vue';
import { useDbStore } from '@/stores/database';
import { ref as refDb } from '@firebase/database';
import { db } from '@/utils/firebase.config';
import { get, onValue, push, set, update } from 'firebase/database';
import { Account } from '@/stores/accounts';

export type Transaction = {
  account: string,
  accountId: string,
  amount: number,
  author: string,
  category: string,
  categoryId: string,
  createdAt: number,
  date: number,
  description: string
  id: string,
  isForecast: boolean,
  isNotPlanned: boolean,
  readonly: boolean,
  type: 'income' | 'outcome' | 'transfer'
}

export type FormDataTransactions = {
  account: string,
  accountId: string,
  amount: number,
  category: string,
  categoryId: string,
  date: number,
  description: string,
  isNotPlanned: boolean,
  readonly: boolean,
  type: 'income' | 'outcome' | 'transfer'
}

export const useTransactionsStore = defineStore('transactions', () => {
  const transactionsData: Ref<Transaction[]> = ref([]);
  const selectedTransactionId: Ref<string> = ref('');
  const counter: Ref<number> = ref(0);
  const transactionDate: Ref<number> = ref(new Date().getTime());
  const transactionsMonthFilter: Ref<number> = ref(new Date().getMonth());
  const transactionsYearFilter: Ref<number> = ref(new Date().getFullYear());

  const transactions: ComputedRef<Transaction[]> = computed(() => Object.values(transactionsData.value));

  // reverse sorting
  const transactionsSortedByDate = computed(() => {
    if (transactionsMonthFilter.value !== 12) {
      return transactions.value
        .filter((tr) => new Date(tr.date).getFullYear() === transactionsYearFilter.value)
        .filter((tr) => new Date(tr.date).getMonth() === transactionsMonthFilter.value)
        .sort((a, b) => b.date - a.date);
    } else {
      return transactions.value
        .filter((tr) => new Date(tr.date).getFullYear() === transactionsYearFilter.value)
        .sort((a, b) => b.date - a.date);
    }
  });

  const outcomeTransactions: ComputedRef<Transaction[]> = computed(() => {
    if (transactionsMonthFilter.value !== 12) {
      return transactions.value.filter((tr) => tr.type === 'outcome')
        .filter((tr) => new Date(tr.date).getFullYear() === transactionsYearFilter.value)
        .filter((tr) => new Date(tr.date).getMonth() === transactionsMonthFilter.value);
    } else {
      return transactions.value
        .filter((tr) => tr.type === 'outcome')
        .filter((tr) => new Date(tr.date).getFullYear() === transactionsYearFilter.value);
    }
  });

  const selectedTransaction: ComputedRef<Transaction | undefined> = computed(() => {
    return transactions.value.find((tr) => tr.id === selectedTransactionId.value);
  });

  const getNewDate = (newDate: Date) => {
    transactionDate.value = newDate.getTime();
  };

  const pushNewTransaction = async (data: FormDataTransactions) => {
    const { baseId, userFullName } = storeToRefs(useDbStore());

    const newTransaction: Transaction = {
      account: data.account,
      accountId: data.accountId,
      amount: data.amount,
      author: userFullName.value,
      category: data.category,
      categoryId: data.categoryId,
      createdAt: new Date().getTime(),
      date: data.date,
      description: data.description,
      id: '',
      isForecast: data.date > new Date().getTime(),
      isNotPlanned: data.isNotPlanned,
      readonly: data.readonly,
      type: data.type
    };

    try {
      const newTransactionRef = push(refDb(db, `bases/${ baseId.value }/transactions/`));
      newTransaction.id = <string>newTransactionRef.key;

      if (newTransaction.isForecast) {
        await set(newTransactionRef, newTransaction);
      } else {
        switch (newTransaction.type) {
          case 'transfer':
            await changeBalanceByTransfer(newTransaction, baseId.value);
            break;
          case 'outcome':
            await changeBalanceByOutcome(newTransaction, baseId.value);
            break;
          default:
            await changeBalanceByIncome(newTransaction, baseId.value);
            break;
        }
        await set(newTransactionRef, newTransaction);
      }
    } catch (e) {
      console.error(e);
    }
  };

  const changeBalanceByIncome = async (data: Transaction, baseId: string) => {
    if (!baseId && !data) return;
    const updates = {};
    const sourceAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.accountId }`);
    const sourceAccountData = await (await get(sourceAccountRef)).val();
    sourceAccountData.balance += data.amount;
    sourceAccountData.balance = Number(sourceAccountData.balance.toFixed(2));
    updates[`bases/${ baseId }/accounts/${ data.accountId }`] = sourceAccountData;
    return update(refDb(db), updates);
  };

  const changeBalanceByOutcome = async (data: Transaction, baseId: string) => {
    if (!baseId && !data) return;
    const updates = {};
    const sourceAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.accountId }`);
    const sourceAccountData = await (await get(sourceAccountRef)).val();
    sourceAccountData.balance -= data.amount;
    sourceAccountData.balance = Number(sourceAccountData.balance.toFixed(2));
    updates[`bases/${ baseId }/accounts/${ data.accountId }`] = sourceAccountData;
    return update(refDb(db), updates);
  };

  const changeBalanceByTransfer = async (data: Transaction, baseId: string) => {
    if (!baseId && !data) return;
    const updates = {};
    const sourceAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.accountId }`);
    const sourceAccountData = await (await get(sourceAccountRef)).val();
    sourceAccountData.balance -= data.amount;
    sourceAccountData.balance = Number(sourceAccountData.balance.toFixed(2));
    updates[`bases/${ baseId }/accounts/${ data.accountId }`] = sourceAccountData;
    const destinationAccountRef = refDb(db, `bases/${ baseId }/accounts/${ data.categoryId }`);
    const destinationAccountData = await (await get(destinationAccountRef)).val();
    destinationAccountData.balance += data.amount;
    destinationAccountData.balance = Number(destinationAccountData.balance.toFixed(2));
    updates[`bases/${ baseId }/accounts/${ data.categoryId }`] = destinationAccountData;
    return update(refDb(db), updates);
  };

  const editTransaction = async (incomingChangesData: FormDataTransactions) => {
    if (!selectedTransaction.value) return;
    if (!incomingChangesData) return;
    const modifiedTransaction: Transaction = selectedTransaction.value;
    const { baseId, userFullName } = storeToRefs(useDbStore());
    const updates = {};
    const isTransfer: boolean = modifiedTransaction.type === 'transfer';
    const isSameAccount: boolean = modifiedTransaction.accountId === incomingChangesData.accountId;
    const isSameDestinationAccount: boolean = modifiedTransaction.categoryId === incomingChangesData.categoryId;

    try {
      const initialAccountRef = refDb(
        db, `bases/${ baseId.value }/accounts/${ modifiedTransaction.accountId }`
      );
      const initialAccount = await (await get(initialAccountRef)).val();

      const initialDestinationAccountRef = refDb(
        db, `bases/${ baseId.value }/accounts/${ modifiedTransaction.categoryId }`
      );
      const initialDestinationAccount = await (await get(initialDestinationAccountRef)).val();

      if (!modifiedTransaction.isForecast) {
        if (modifiedTransaction.type === 'income') {
          initialAccount.balance -= modifiedTransaction.amount;
          initialAccount.balance = Number(initialAccount.balance.toFixed(2));
        } else if (modifiedTransaction.type === 'outcome') {
          initialAccount.balance += modifiedTransaction.amount;
          initialAccount.balance = Number(initialAccount.balance.toFixed(2));
        } else if (isTransfer) {
          initialAccount.balance += modifiedTransaction.amount;
          initialAccount.balance = Number(initialAccount.balance.toFixed(2));
          initialDestinationAccount.balance -= modifiedTransaction.amount;
          initialDestinationAccount.balance = Number(initialDestinationAccount.balance.toFixed(2));

          updates[`bases/${ baseId.value }/accounts/${ initialDestinationAccount.id }`] = initialDestinationAccount;
        }

        updates[`bases/${ baseId.value }/accounts/${ initialAccount.id }`] = initialAccount;
      }

      Object.keys(modifiedTransaction).forEach((key) => {
        if (Object.hasOwn(incomingChangesData, key)) modifiedTransaction[key] = incomingChangesData[key];
        if (key === 'author') modifiedTransaction[key] = userFullName.value;
        if (key === 'isForecast') modifiedTransaction[key] = modifiedTransaction.date > new Date().getTime();
      });

      updates[`bases/${ baseId.value }/transactions/${ modifiedTransaction.id }`] = modifiedTransaction;

      let modifiedAccount = initialAccount;
      let resultDestinationAccount = initialDestinationAccount;

      if (!modifiedTransaction.isForecast) {
        if (isTransfer) {
          if (isSameAccount && isSameDestinationAccount) {
            modifiedAccount = initialAccount;
            resultDestinationAccount = initialDestinationAccount;
          } else if (isSameAccount && !isSameDestinationAccount) {
            modifiedAccount = initialAccount;
            const resultDestinationAccountRef = refDb(
              db, `bases/${ baseId.value }/accounts/${ modifiedTransaction.categoryId }`
            );
            resultDestinationAccount = await (await get(resultDestinationAccountRef)).val();
          } else if (!isSameAccount && isSameDestinationAccount) {
            const modifiedAccountRef = refDb(
              db, `bases/${ baseId.value }/accounts/${ modifiedTransaction.accountId }`
            );
            modifiedAccount = await (await get(modifiedAccountRef)).val();
            resultDestinationAccount = initialDestinationAccount;
          } else if (!isSameAccount && !isSameDestinationAccount) {
            const modifiedAccountRef = refDb(
              db, `bases/${ baseId.value }/accounts/${ modifiedTransaction.accountId }`
            );
            modifiedAccount = await (await get(modifiedAccountRef)).val();
            const resultDestinationAccountRef = refDb(
              db, `bases/${ baseId.value }/accounts/${ modifiedTransaction.categoryId }`
            );
            resultDestinationAccount = await (await get(resultDestinationAccountRef)).val();
          }
        } else {
          if (isSameAccount) {
            modifiedAccount = initialAccount;
          } else {
            const modifiedAccountRef = refDb(
              db, `bases/${ baseId.value }/accounts/${ modifiedTransaction.accountId }`
            );
            modifiedAccount = await (await get(modifiedAccountRef)).val();
          }
        }

        if (modifiedTransaction.type === 'income') {
          modifiedAccount.balance += modifiedTransaction.amount;
          modifiedAccount.balance = Number(modifiedAccount.balance.toFixed(2));
        } else if (modifiedTransaction.type === 'outcome') {
          modifiedAccount.balance -= modifiedTransaction.amount;
          modifiedAccount.balance = Number(modifiedAccount.balance.toFixed(2));
        } else if (isTransfer) {
          modifiedAccount.balance -= modifiedTransaction.amount;
          modifiedAccount.balance = Number(modifiedAccount.balance.toFixed(2));
          resultDestinationAccount.balance += modifiedTransaction.amount;
          resultDestinationAccount.balance = Number(resultDestinationAccount.balance.toFixed(2));

          updates[`bases/${ baseId.value }/accounts/${ resultDestinationAccount.id }`] = resultDestinationAccount;
        }

        updates[`bases/${ baseId.value }/accounts/${ modifiedAccount.id }`] = modifiedAccount;
      }

      return update(refDb(db), updates);
    } catch (e) {
      console.error(e);
    }
  };

  const changeTransactionToRegular = async () => {
    if (!selectedTransaction.value) return;
    const updates = {};
    const { baseId } = storeToRefs(useDbStore());
    try {
      const accountRef = refDb(
        db, `bases/${ baseId.value }/accounts/${ selectedTransaction.value.accountId }`
      );
      const transactionRef = refDb(
        db, `bases/${ baseId.value }/transactions/${ selectedTransaction.value.id }`
      );
      const account = await (await get(accountRef)).val();
      const transaction = await (await get(transactionRef)).val();
      account.balance = Number((account.balance - transaction.amount).toFixed(2));
      transaction.isForecast = false;
      updates[`bases/${ baseId.value }/accounts/${ account.id }`] = account;
      updates[`bases/${ baseId.value }/transactions/${ transaction.id }`] = transaction;
      return update(refDb(db), updates);
    } catch (e) {
      console.log(e);
    }
  };

  const deleteTransaction = async () => {
    if (!selectedTransaction.value) return;
    try {
      const { baseId } = storeToRefs(useDbStore());
      const transactionRef = refDb(
        db, `bases/${ baseId.value }/transactions/${ selectedTransactionId.value }`
      );
      const initialAccountRef = refDb(
        db, `bases/${ baseId.value }/accounts/${ selectedTransaction.value.accountId }`
      );
      const initialAccount = await (await get(initialAccountRef)).val();

      let destinationAccount: Account;

      if (!selectedTransaction.value?.isForecast) {
        // rollback initial transaction on initial account
        if (selectedTransaction.value.type === 'income') {
          initialAccount.balance -= selectedTransaction.value.amount;
          initialAccount.balance = Number(initialAccount.balance.toFixed(2));
        } else if (selectedTransaction.value.type === 'outcome') {
          initialAccount.balance += selectedTransaction.value.amount;
          initialAccount.balance = Number(initialAccount.balance.toFixed(2));
        } else if (selectedTransaction.value.type === 'transfer') {
          const destinationAccountRef = refDb(
            db, `bases/${ baseId.value }/accounts/${ selectedTransaction.value?.categoryId }`
          );
          destinationAccount = await (await get(destinationAccountRef)).val();

          initialAccount.balance += selectedTransaction.value.amount;
          initialAccount.balance = Number(initialAccount.balance.toFixed(2));
          destinationAccount.balance -= selectedTransaction.value.amount;
          destinationAccount.balance = Number(destinationAccount.balance.toFixed(2));

          await set(destinationAccountRef, destinationAccount);
        }

        await set(initialAccountRef, initialAccount);
      }
      selectedTransactionId.value = '';
      await set(transactionRef, null);
    } catch (e) {
      console.error(e);
    }
  };

  const subscribeToTransactions = async () => {
    try {
      const { baseId } = storeToRefs(useDbStore());
      const transactionsRef = refDb(db, `bases/${ baseId.value }/transactions/`);
      onValue(transactionsRef, async (snapshot) => {
        if (snapshot.exists()) {
          transactionsData.value = snapshot.val();
        } else if (counter.value < 5) {
          setTimeout(() => {
            counter.value++;
            subscribeToTransactions();
          }, (counter.value + 1) * 100);
        } else {
          transactionsData.value = [];
          console.error('Не существует пути до ТРАНЗАКЦИЙ');
        }
      });
    } catch (e) {
      console.error(e);
    }
  };

  return {
    transactions,
    selectedTransactionId,
    selectedTransaction,
    transactionDate,
    transactionsMonthFilter,
    transactionsYearFilter,
    transactionsSortedByDate,
    outcomeTransactions,
    pushNewTransaction,
    editTransaction,
    changeTransactionToRegular,
    deleteTransaction,
    subscribeToTransactions,
    getNewDate
  };
});