<template>
  <div id="modalSetLimit" ref="setLimitModal" class="modal">
    <div class="modal-content">
      <h4>Установка лимита</h4>
      <button
        class="btn modal-close-btn modal-close red">
        <i class="large material-icons">close</i>
      </button>
      <form class="mc-form-category" @submit.prevent="submitLimitForm">
        <div class="row">
          <div class="col s12">
            <div class="input-field col s12">
              <select v-model="selectedCategoryId">
                <option v-for="cat in categoriesAvailableForLimitation" :key="cat.id!" :value="cat.id">
                  {{ cat.name }}
                </option>
              </select>
              <label>На какую категорию установить лимит?</label>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input id="limitAmount" v-model.number="setLimitValue" type="number">
            <label for="limitAmount">Сумма лимита (введите 0, чтобы убрать лимит)</label>
          </div>
        </div>
        <div class="row">
          <div class="col">
            <button class="mc-button-submit btn" name="action" type="submit">
              Установить лимит
              <i class="material-icons right">upload</i>
            </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { onBeforeUnmount, onMounted, ref } from 'vue';
import { storeToRefs } from 'pinia';
import { useCategoriesStore } from '@/stores/categories';
import { LimitData, usePlanningStore } from '@/stores/limits';
import M from 'materialize-css';

const setLimitModal = ref();
const modalInstance = ref();

const { selectedCategoryId, categoriesAvailableForLimitation } = storeToRefs(useCategoriesStore());
const { setLimitValue } = storeToRefs(usePlanningStore());
const { pushNewLimit } = usePlanningStore();

const submitLimitForm = async () => {
  const formData: LimitData = {
    categoryId: selectedCategoryId.value,
    limit: setLimitValue.value,
    id: ''
  };
  try {
    await pushNewLimit(formData);
  } catch (e) {
    console.log(e);
  } finally {
    selectedCategoryId.value = '';
    setLimitValue.value = 0;
    modalInstance.value.close();
  }
};

onMounted(() => {
  modalInstance.value = M.Modal.init(setLimitModal.value);
  M.updateTextFields();
});

onBeforeUnmount(() => {
  modalInstance.value.destroy();
});
</script>

<style lang="scss" scoped>
.modal-content {
  position: relative;
  min-height: 70vh;

  @media (orientation: portrait) and (max-width: 600px) {
    padding-top: 50px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 24px;
  right: 24px;

  @media (orientation: portrait) and (max-width: 600px) {
    top: 10px;
    right: 10px;
  }
}
</style>
