import { defineStore, storeToRefs } from 'pinia';
import { useDbStore } from '@/stores/database';
import { useCategoriesStore } from '@/stores/categories';
import {
  FormDataTransactions,
  useTransactionsStore
} from '@/stores/transactions';
import { usePlanningStore } from '@/stores/limits';
import { computed, ComputedRef, Ref, ref } from 'vue';
import { db } from '@/utils/firebase.config';
import {
  ref as refDb,
  onValue,
  push,
  set,
  get,
  update
} from 'firebase/database';
import PINIA from '@/stores/index';

export type Account = {
  name: string,
  balance: number,
  id: string,
  isCountable: boolean,
  initialBalance: number
}

export type Data = {
  accountName: string,
  balance: number,
  isCountable: boolean
}

export const useAccountsStore = defineStore('accounts', () => {
  const accountsData: Ref<Account[]> = ref([]);
  const counter: Ref<number> = ref(0);
  const selectedAccountId: Ref<string> = ref('');
  const destinationAccountId: Ref<string> = ref('');
  const transferAmount: Ref<number> = ref(0);
  const requiredAmountForTransfer: Ref<number> = ref(0);
  const { pushNewTransaction } = useTransactionsStore();

  const accounts: ComputedRef<Account[]> = computed(() => {
    return Object.values(accountsData.value)
      .sort((a, b) => {
        if (a.name > b.name) {
          return 1;
        } else if (a.name < b.name) {
          return -1;
        } else return 0;
      })
      .sort((a, b) => {
        if (a.isCountable && !b.isCountable) {
          return -1;
        } else if (!a.isCountable && b.isCountable) {
          return 1;
        } else return 0;
      });
  });

  const selectedAccountName: ComputedRef<string> = computed((): string => {
    const account = accounts.value.find((account) => account.id === selectedAccountId.value);
    return account ? account.name : '';
  });

  const selectedAccountBalance: ComputedRef<number> = computed((): number => {
    const account = accounts.value.find((account) => account.id === selectedAccountId.value);
    return account ? account.balance : 0;
  });

  const destinationAccountName: ComputedRef<string> = computed((): string => {
    return accounts.value.find((account) => account.id === destinationAccountId.value)?.name || '';
  });

  const isSourceAccountBalanceNotEnough: ComputedRef<boolean> = computed((): boolean => {
    if (!!selectedAccountId.value) {
      const sourceAccountBalance: number = accounts.value.find((acc: Account) => acc.id === selectedAccountId.value)?.balance || 0;
      requiredAmountForTransfer.value = sourceAccountBalance - transferAmount.value;
      return sourceAccountBalance < transferAmount.value;
    } else {
      return true;
    }
  });

  const allAccountsSum: ComputedRef<number> = computed((): number => accounts.value.reduce((sum, currentAccount) => {
    if (currentAccount.isCountable) {
      return sum + currentAccount.balance;
    } else {
      return sum;
    }
  }, 0));

  const pushNewAccount = async (data: Data) => {
    const { baseId } = storeToRefs(useDbStore(PINIA));

    const newAccount: Account = {
      name: data.accountName,
      balance: data.balance,
      id: '',
      isCountable: data.isCountable,
      initialBalance: data.balance
    };

    try {
      const newAccountRef = push(refDb(db, `bases/${ baseId.value }/accounts/`));
      if (typeof newAccountRef.key === 'string') {
        newAccount.id = newAccountRef.key;
      }
      await set(newAccountRef, newAccount);
    } catch (e) {
      console.error(e);
    } finally {
      await subscribeToAccounts();
    }
  };

  const changeAccount = async (data: Data) => {
    try {
      const updates = {};
      const { baseId } = storeToRefs(useDbStore());
      const accountRef = refDb(db, `bases/${ baseId.value }/accounts/${ selectedAccountId.value }`);
      const accountData = await (await get(accountRef)).val();
      const accountTransactionType: 'income' | 'outcome' = accountData.balance > data.balance ? 'outcome' : 'income';
      const accountTransactionAmount: number = Number(Math.abs((accountData.balance - data.balance)).toFixed(2));
      accountData.name = data.accountName;
      accountData.balance = data.balance;
      accountData.isCountable = data.isCountable;
      updates[`bases/${ baseId.value }/accounts/${ selectedAccountId.value }`] = accountData;
      selectedAccountId.value = '';
      const year: number = new Date().getFullYear();
      const month: number = new Date().getMonth();
      const day: number = new Date().getDate();

      const accountTransactionData: FormDataTransactions = {
        account: accountData.name,
        accountId: accountData.id,
        amount: accountTransactionAmount,
        category: accountData.name,
        categoryId: accountData.id,
        date: new Date(year, month, day).getTime(),
        description: `Корректировка (${ accountTransactionType === 'income' ? 'рост' : 'понижение' }) баланса счёта ${ accountData.name.toUpperCase() } на сумму ${ Math.abs(accountTransactionAmount) }`,
        isNotPlanned: false,
        readonly: true,
        type: accountTransactionType
      };

      await pushNewTransaction(accountTransactionData);
      return update(refDb(db), updates);
    } catch (e) {
      console.error(e);
    }
  };

  const deleteAccount = async () => {
    try {
      const { baseId } = storeToRefs(useDbStore());
      const accountRef = refDb(db, `bases/${ baseId.value }/accounts/${ selectedAccountId.value }`);
      selectedAccountId.value = '';
      await set(accountRef, null);
    } catch (e) {
      console.error(e);
    }
  };

  const subscribeToAccounts = async () => {
    const { subscribeToCategories } = useCategoriesStore();
    const { subscribeToTransactions } = useTransactionsStore();
    const { subscribeToLimits } = usePlanningStore();
    try {
      const { baseId } = storeToRefs(useDbStore());
      if (!!baseId.value) {
        const balanceRef = refDb(db, `bases/${ baseId.value }/accounts/`);
        onValue(balanceRef, async (snapshot) => {
          if (snapshot.exists()) {
            accountsData.value = snapshot.val();
            await subscribeToCategories();
            await subscribeToTransactions();
            await subscribeToLimits();
          } else if (counter.value < 5) {
            setTimeout(() => {
              counter.value++;
              subscribeToAccounts();
            }, (counter.value + 1) * 100);
          } else {
            accountsData.value = [];
            console.error('Не существует пути до СЧЕТОВ');
          }
        });
      } else {
        console.error('BaseID is empty or null');
      }
    } catch (e) {
      console.error(e);
    }
  };

  return {
    accounts,
    selectedAccountId,
    destinationAccountId,
    destinationAccountName,
    transferAmount,
    requiredAmountForTransfer,
    isSourceAccountBalanceNotEnough,
    selectedAccountName,
    selectedAccountBalance,
    allAccountsSum,
    subscribeToAccounts,
    pushNewAccount,
    changeAccount,
    deleteAccount
  };
});