<template>
  <div id="modalEditTransaction" ref="editTransactionModal" class="modal">
    <div class="modal-content">
      <h4>Изменить данные</h4>
      <button
        class="btn modal-close-btn modal-close red">
        <i class="large material-icons">close</i>
      </button>
      <div class="row">
        <form
          class="col s12"
          @submit.prevent="submitTransactionChanges">
          <div class="row">
            <div class="input-field col s12">
              <select
                v-model="selectedAccountId">
                <option v-for="acc in accounts" :key="acc.id" :value="acc.id">
                  {{ acc.name }} | {{ currencyFilter(acc.balance) }}
                </option>
              </select>
              <label>Счёт</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <select
                v-model="selectedCategoryId">
                <option
                  v-for="cat in categories"
                  :key="cat.id!"
                  :value="cat.id">
                  {{ cat.name }} | {{
                    cat.type === 'outcome' ? 'Расход' : 'Доход'
                  }}
                </option>
              </select>
              <label>Категория</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <i class="material-icons prefix">calendar_month</i>
              <input
                id="recordDateEdit"
                ref="datepicker"
                class="datepicker"
                name="recordDateEdit"
                type="text">
              <label for="recordDateEdit">Дата</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <i class="material-icons prefix">currency_ruble</i>
              <input
                id="amount"
                v-model.number="state.transactionAmount"
                :class="{ invalid: isTransactionRequiredInvalid || isTransactionMinValueInvalid }"
                step="0.01"
                type="number">
              <label for="amount">Сумма</label>
              <!--suppress TypeScriptUnresolvedReference, JSUnresolvedReference -->
              <small
                v-if="isTransactionRequiredInvalid"
                class="helper-text red-text"
              >{{ v$.transactionAmount.required.$message }}</small>
              <!--suppress TypeScriptUnresolvedReference, JSUnresolvedReference -->
              <small
                v-else-if="isTransactionMinValueInvalid"
                class="helper-text red-text"
              >{{ v$.transactionAmount.minValue.$message }}</small>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <i class="material-icons prefix">edit</i>
              <input
                id="description"
                v-model="state.transactionDescription"
                type="text">
              <label for="description">Комментарий</label>
            </div>
          </div>
          <div class="row valign-wrapper">
            <div class="col s12 m4">
              <button
                :disabled="!isFormFilled"
                class="mc-button-submit btn light-green darken-4"
                name="action"
                type="submit">
                Обновить
                <i class="material-icons right">upload</i>
              </button>
            </div>
            <div class="col s12 m8">
              <label>
                <input v-model="state.isNotPlanned" class="filled-in" type="checkbox"/>
                <span class="red-text">Незапланированный расход?</span>
              </label>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script lang="ts" setup>
import M from 'materialize-css';
import { computed, onBeforeUnmount, onMounted, onUpdated, reactive, Ref, ref } from 'vue';
import { storeToRefs } from 'pinia';
import { useAccountsStore } from '@/stores/accounts';
import { useCategoriesStore } from '@/stores/categories';
import { useFiltersStore } from '@/stores/filters';
import { FormDataTransactions, useTransactionsStore } from '@/stores/transactions';
import { datepickerOptions } from '@/utils/datepicker.options';
import { helpers, minValue, required } from '@vuelidate/validators';
import { useVuelidate } from '@vuelidate/core';

const { accounts, selectedAccountId, selectedAccountName } = storeToRefs(useAccountsStore());
const { categories, selectedCategoryId, selectedCategory } = storeToRefs(useCategoriesStore());
const { selectedTransaction, transactionDate } = storeToRefs(useTransactionsStore());
const { editTransaction } = useTransactionsStore();
const { currencyFilter } = useFiltersStore();

const editTransactionModal = ref();
const modalInstance = ref();
const datepicker = ref();
const datepickerInstance = ref();
const selectInstances = ref();

// validation setup
const transactionMinValue: Ref<number> = ref(0.01);
const state = reactive({
  transactionAmount: 0,
  transactionDescription: '',
  isNotPlanned: false
});
const rules = {
  transactionAmount: {
    required: helpers.withMessage('Поле СУММА является обязательным.', required),
    minValue: helpers.withMessage(({ $params }) => `Сумма должна быть не менее, чем ${ $params.min } рублей.`, minValue(transactionMinValue))
  }
};
const v$ = useVuelidate(rules, state);
const isTransactionRequiredInvalid = computed(() => {
  return v$.value.transactionAmount.$dirty && v$.value.transactionAmount.required.$invalid;
});
const isTransactionMinValueInvalid = computed(() => {
  return v$.value.transactionAmount.$dirty && v$.value.transactionAmount.minValue.$invalid;
});

const isFormFilled = computed(() => {
  return !!(selectedCategory.value && selectedAccountName && !v$.value.transactionAmount.$invalid);
});

const submitTransactionChanges = async () => {
  const formData: FormDataTransactions = {
    account: selectedAccountName.value,
    accountId: selectedAccountId.value,
    amount: Number(state.transactionAmount.toFixed(2)),
    category: <string>selectedCategory.value?.name,
    categoryId: selectedCategoryId.value,
    date: transactionDate.value,
    description: state.transactionDescription,
    isNotPlanned: state.isNotPlanned,
    type: <'outcome' | 'income' | 'transfer'>selectedCategory.value?.type
  };
  await editTransaction(formData);
  modalInstance.value.close();
};

// noinspection JSUnusedGlobalSymbols
const modalOptions = {
  onOpenStart: () => {
    selectedAccountId.value = <string>selectedTransaction.value?.accountId;
    selectedCategoryId.value = <string>selectedTransaction.value?.categoryId;
    state.transactionAmount = <number>selectedTransaction.value?.amount;
    state.transactionDescription = <string>selectedTransaction.value?.description;
    state.isNotPlanned = <boolean>selectedTransaction.value?.isNotPlanned;
  },
  onOpenEnd: () => {
    M.updateTextFields();
  },
  onCloseEnd: () => {
    selectedAccountId.value = '';
    selectedCategoryId.value = '';
  }
};

onUpdated(() => {
  selectInstances.value = document.querySelectorAll('select');
  M.FormSelect.init(selectInstances.value);
  M.updateTextFields();
});

onMounted(() => {
  modalInstance.value = M.Modal.init(editTransactionModal.value, modalOptions);
  M.FormSelect.init(selectInstances.value);
  M.updateTextFields();
  datepickerInstance.value = M.Datepicker.init(datepicker.value, datepickerOptions);
});

onBeforeUnmount(() => {
  modalInstance.value.destroy();
  datepickerInstance.value.destroy();
});
</script>
<style lang="scss" scoped>
.modal-content {
  position: relative;

  @media (orientation: portrait) and (max-width: 600px) {
    padding-top: 50px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 24px;
  right: 24px;

  @media (orientation: portrait) and (max-width: 600px) {
    top: 10px;
    right: 10px;
  }
}
</style>