<template>
  <div class="mc-main-background green lighten-5">
    <component :is="layoutComponent">
      <router-view/>
    </component>
  </div>
  <div
    v-if="areBaseAndCategoriesReady"
    class="fixed-action-btn">
    <button
      ref="addTransaction"
      class="btn-floating btn-large red modal-trigger"
      data-target="modalAddTransaction">
      <i class="material-icons">add</i>
    </button>
  </div>
  <Teleport to="body">
    <ModalComponent/>
  </Teleport>
</template>

<script lang="ts" setup>
import ModalComponent from '@/components/ModalComponent.vue';
import {
  onBeforeMount,
  onMounted,
  ref,
  Ref,
  Component,
  defineAsyncComponent,
  shallowRef,
  watch
} from 'vue';
import { useAccountsStore } from '@/stores/accounts';
import { auth } from '@/utils/firebase.config';
import { storeToRefs } from 'pinia';
import { useDbStore } from '@/stores/database';
import { useStatusStore } from '@/stores/status';
import { useRoute } from 'vue-router';

const { isDataNeeded, areBaseAndCategoriesReady } = storeToRefs(useStatusStore());
const { fetchUserInfo } = useDbStore();
const { subscribeToAccounts } = useAccountsStore();
const route = useRoute();
const layoutComponent = shallowRef<Component>();

const addTransaction: Ref<HTMLButtonElement | undefined> = ref();

watch(
  () => route.meta.layout ?? 'MainLayout',
  (layout) => {
    // noinspection TypeScriptCheckImport
    layoutComponent.value = defineAsyncComponent(() => import(`@/layouts/${ layout }.vue`)
    );
  },
  { immediate: true }
);

onBeforeMount(async () => {
  if (!!auth.currentUser && isDataNeeded.value) {
    await fetchUserInfo();
    await subscribeToAccounts();
    isDataNeeded.value = false;
  }
});

//TODO Add data fetching after login

onMounted(() => {
  if (addTransaction.value) {
    M.FloatingActionButton.init(addTransaction.value, { direction: 'right' });
  }
});
</script>

<style lang="scss">
@import '~materialize-css/dist/css/materialize.min.css';
@import 'scss/table-scrollable';

.mc-main-background {
  min-height: 100vh;
  height: auto;
}

.datepicker-modal {
  min-width: 700px;
}
</style>